import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});

export default function SeniorPrj_card() {
  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <CardActionArea
        target="_blank"
        href="https://drive.google.com/file/d/19LoR_ePImNTTQV1Om-6utxzuFWfpdgEn/view?usp=sharing"
      >
        <CardMedia
          className={classes.media}
          image={require('../../../img/projects/SeniorPrj.PNG')}
          title="Senior Project"
          style={{ height: 0, paddingTop: '75%' }}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            My Senior Project
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            This project involved the research for how Washington River
            Protection Solutions could use machine learning to help the planning
            of their radioactive waste clean-up project.
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button
          size="small"
          color="primary"
          target="_blank"
          href="https://drive.google.com/file/d/1LmrgTEhp9oZvoZQgmf7QzOvRe56Jxoni/view?usp=sharing"
        >
          View Thesis
        </Button>
        <Button
          size="small"
          color="primary"
          target="_blank"
          href="https://drive.google.com/file/d/19LoR_ePImNTTQV1Om-6utxzuFWfpdgEn/view?usp=sharing"
        >
          Watch Presentation
        </Button>
      </CardActions>
    </Card>
  );
}
